  font-family: 'Open Sauce One', sans-serif;
  font-size: 14px;
  color: #112850;
}

.tabsContainer{
  display: flex;
  width: fit-content;
  margin: auto;
  padding: 8px;
  background-color: #ffffff;
  border: 1px solid #CCCCCC;
  border-radius: 48px;
  margin: 0 auto 30px auto;
}

.tabsContainer .tab{
  display: flex;
  background-color: #ffffff;
  color: #112850;
  border: none;
  align-items: center;
  justify-content: center;
  height: 44px;
  width: 170px;
  padding: 8px;
  border-radius: 48px;
  margin: auto 8px;
}

.tabsContainer .tab:hover{
  background-color: #E3146533;
  cursor: pointer;
}

.tabsContainer .tab.active{
  background-color: #E31465;
  color: #ffffff;
}

table{
  border: 1px solid #ddd; 
}

table thead{
  background-color: #1128500D;
  border-bottom: 1px solid #ddd;
  height: 48px;
}

table td{
  border-bottom: 1px solid #ddd; 
  padding: 8px;
}

.previewContainer {
  display: flex;
  align-items: center;
}

.previewContainer div {
  margin: 0 8px;
}

.preview {
  width: 50px;
  height: 50px;
  border: 1px solid #CCCCCC;
  display: inline-block;
  border-radius: 8px;
}

.tooltip {
  background-color: rgba(0, 0, 0, 0.8);
  color: #fff;
  border-radius: 4px;
  z-index: 999;
  margin: 0;
  font-size: .8rem;
  padding:8px;
}


/* Tooltip arrow */
.tooltip::after {
  content: "";
  position: absolute;
  top: 100%;
  left: 50%;
  margin-left: -5px;
  border-width: 5px;
  border-style: solid;
  border-color: rgba(0, 0, 0, 0.8) transparent transparent transparent;
}


.grid {
  content: "";
  display: block;
  position: relative;
  z-index: -1;
  top: 0;
  left: -8px;
  width: 50px;
  height: 50px;
  border-radius: 8px;
  background-image: url(${___CSS_LOADER_URL_REPLACEMENT_0___});
  background-size: contain;
}

.textColor{
  padding: 10px;
  display: inline-block;
  background: rgb(248, 248, 248);
    padding: 4px 8px;
    border: 1px solid rgb(238, 238, 238);
    border-radius: 4px;
}

.copy_icon{
  margin-left: 8px;
}

.copy_icon:hover{
  cursor: pointer;
}

.textColor:hover{
  background-color: #D4D9E8;
}

input{
  width: -webkit-fill-available;
  background-color : #1128500D;
  padding: 8px 8px 8px 40px;
  border: 2px solid transparent;
  border-radius: 16px;
  color: rgb(25, 32, 56);
  font-size: 15px;
  font-weight:  600;
  height: 24px;
  letter-spacing: normal;
  line-height: 24px;
  background: #1128500D url(${___CSS_LOADER_URL_REPLACEMENT_1___}) no-repeat 13px center;
}

input:focus-visible{
  outline: 1px solid #B9C0CF;
}`, "",{"version":3,"sources":["webpack://./stories/designTokensDisplay.css"],"names":[],"mappings":"AAEA;EACE,yCAAyC;EACzC,eAAe;EACf,cAAc;AAChB;;AAEA;EACE,aAAa;EACb,kBAAkB;EAClB,YAAY;EACZ,YAAY;EACZ,yBAAyB;EACzB,yBAAyB;EACzB,mBAAmB;EACnB,wBAAwB;AAC1B;;AAEA;EACE,aAAa;EACb,yBAAyB;EACzB,cAAc;EACd,YAAY;EACZ,mBAAmB;EACnB,uBAAuB;EACvB,YAAY;EACZ,YAAY;EACZ,YAAY;EACZ,mBAAmB;EACnB,gBAAgB;AAClB;;AAEA;EACE,2BAA2B;EAC3B,eAAe;AACjB;;AAEA;EACE,yBAAyB;EACzB,cAAc;AAChB;;AAEA;EACE,sBAAsB;AACxB;;AAEA;EACE,2BAA2B;EAC3B,6BAA6B;EAC7B,YAAY;AACd;;AAEA;EACE,6BAA6B;EAC7B,YAAY;AACd;;AAEA;EACE,aAAa;EACb,mBAAmB;AACrB;;AAEA;EACE,aAAa;AACf;;AAEA;EACE,WAAW;EACX,YAAY;EACZ,yBAAyB;EACzB,qBAAqB;EACrB,kBAAkB;AACpB;;AAEA;EACE,oCAAoC;EACpC,WAAW;EACX,kBAAkB;EAClB,YAAY;EACZ,SAAS;EACT,gBAAgB;EAChB,WAAW;AACb;;;AAGA,kBAAkB;AAClB;EACE,WAAW;EACX,kBAAkB;EAClB,SAAS;EACT,SAAS;EACT,iBAAiB;EACjB,iBAAiB;EACjB,mBAAmB;EACnB,oEAAoE;AACtE;;;AAGA;EACE,WAAW;EACX,cAAc;EACd,kBAAkB;EAClB,WAAW;EACX,MAAM;EACN,UAAU;EACV,WAAW;EACX,YAAY;EACZ,kBAAkB;EAClB,yDAAwC;EACxC,wBAAwB;AAC1B;;AAEA;EACE,aAAa;EACb,qBAAqB;EACrB,8BAA8B;IAC5B,gBAAgB;IAChB,oCAAoC;IACpC,kBAAkB;AACtB;;AAEA;EACE,gBAAgB;AAClB;;AAEA;EACE,eAAe;AACjB;;AAEA;EACE,yBAAyB;AAC3B;;AAEA;EACE,6BAA6B;EAC7B,4BAA4B;EAC5B,yBAAyB;EACzB,6BAA6B;EAC7B,mBAAmB;EACnB,sBAAsB;EACtB,eAAe;EACf,iBAAiB;EACjB,YAAY;EACZ,sBAAsB;EACtB,iBAAiB;EACjB,mFAA+Y;AACjZ;;AAEA;EACE,0BAA0B;AAC5B","sourcesContent":["@import url('https://fonts.cdnfonts.com/css/open-sauce-one');\n\nbody{\n  font-family: 'Open Sauce One', sans-serif;\n  font-size: 14px;\n  color: #112850;\n}\n\n.tabsContainer{\n  display: flex;\n  width: fit-content;\n  margin: auto;\n  padding: 8px;\n  background-color: #ffffff;\n  border: 1px solid #CCCCCC;\n  border-radius: 48px;\n  margin: 0 auto 30px auto;\n}\n\n.tabsContainer .tab{\n  display: flex;\n  background-color: #ffffff;\n  color: #112850;\n  border: none;\n  align-items: center;\n  justify-content: center;\n  height: 44px;\n  width: 170px;\n  padding: 8px;\n  border-radius: 48px;\n  m